<template>
	<div id="mall-order-create">
		<public-header title="确认订单"></public-header>
		<van-submit-bar :currency="Lang.dollarSign" :disabled="!islegal" :price="allorder.final_fee * 100" label="待支付" button-text="提交订单" @submit="onSubmit">
			<div class="order-benefit" slot="default">
				已优惠 {{Lang.dollarSign}}{{allorder.discount_fee}}
			</div>
		</van-submit-bar>
		<div class="content">
			<div class="delivery-info margin-10-b bg-default radius-3">
				<ul class="delivery-mode">
					<li class="delivery" :class="{active:  allorder.order_type == 1}" @click="onChangeOrderType(1)">{{0 ? '商家自送' : '骑手配送'}}</li>
					<li class="self" :class="{active: allorder.order_type == 2}" @click="onChangeOrderType(2)">到店自取 <van-tag type="danger" v-if="0">有优惠</van-tag></li>
				</ul>
				<div class="plateform-box" v-show="allorder.order_type == 1">
					<div class="plateform-inner">
						<van-cell-group class="border-0px">
							<van-cell class="addrress-select" title="选择收货地址" icon="plus" is-link v-if="!allorder.address.address" @click="util.jsUrl( '/pages/member/address', query, 'replace')" />
							<van-cell icon="locationfill" is-link class="store-address" @click="util.jsUrl( '/pages/member/address', query, 'replace')" v-else>
								<template slot="title">
									<div class="member-address">
										<template v-if="allorder.address.tag == '1'">
											<span class="tag-address tag-address-yellow">家</span>
										</template>
										<template v-else-if="allorder.address.tag == '2'">
											<span class="tag-address tag-address-blue">公司</span>
										</template>
										<template v-else-if="allorder.address.tag == '3'">
											<span class="tag-address tag-address-green">学校</span>
										</template>
										<span>{{allorder.address.address}}-{{allorder.address.number}}</span>
									</div>
									<div class="store-title font-12 c-gray">{{allorder.address.realname}} {{allorder.address.sex}} {{allorder.address.mobile}}</div>
								</template>
							</van-cell>
							<van-cell :title="allorder.deliveryTimes.predict_day_cn + ' ' + allorder.deliveryTimes.predict_time_cn" icon="timefill" @click="onChangePopup('deliveryTime')" is-link />
						</van-cell-group>
					</div>
				</div>
				<div class="instore-box" v-show="allorder.order_type == 2">
					<div class="instore-inner">
						<van-cell-group class="border-0px">
							<van-cell title="自取时间" :value="`${allorder.deliveryTimes.predict_day_cn} ${allorder.deliveryTimes.predict_time_cn}`"  icon="timefill" @click="onChangePopup('deliveryTime')" is-link></van-cell>
							<van-field
								v-model="allorder.mobile"
								label="预留电话"
								left-icon="mobilefill"
								input-align="right"
								placeholder="请输入自提人手机号"
								@blur="onBlur"
							/>
						</van-cell-group>
						<!--<van-row class="font-14 padding-10-b">
							<van-col span="12">
								<div class="van-hairline&#45;&#45;right padding-15-l padding-10-r">
									<div class="c-gray margin-5-b" >自取时间</div>
									<div class="flex-lr" @click="onChangePopup('deliveryTime')">
										<div class="flex-1">2020-08-15 11:30</div>
										<i class="van-icon van-icon-arrow van-cell__right-icon"></i>
									</div>
								</div>
							</van-col>
							<van-col span="12">
								<div class="padding-15-r padding-10-l">
									<div class="c-gray margin-5-b">预留电话</div>
									<van-row type="flex" align="center">
										<van-col span="20">
											<input v-model="mobile" placeholder="请输入自提人手机号" type="text" class="w-100" @blur="onBlur">
										</van-col>
										<van-col span="4" class="text-right">
											<i class="van-icon van-icon-arrow van-cell__right-icon"></i>
										</van-col>
									</van-row>
								</div>
							</van-col>
						</van-row>-->
					</div>
				</div>
			</div>
			<template v-for="(cartItem, cartIndex) in carts">
				<div class="order-food">
					<div class='order-food-title'>
						<div class='food-shop'>{{cartItem.store.title}}</div>
						<div class='order-tips-thirpart' v-if="cartItem.store.delivery_mode == 2 && cartItem.store.delivery_type != 2">{{cartItem.store.delivery_title}}</div>
					</div>
					<div class="food-list">
						<template v-for="item in cartItem.cart.data">
							<template v-for="option in item">
								<van-card :thumb="option.thumb" v-if="option.goods_id != '88888' && option.is_show == 1 && option.issubmit">
									<div slot="title" class="food-title">
										<div class="left">
											<img class="activity-img" src="static/img/bargain_b.png" alt="" v-if="option.bargain_id > 0 && option.bargain_type == 'bargain'">
											{{option.title}}
										</div>
										<div class="right">{{Lang.dollarSign}}{{option.total_discount_price_show}}</div>
									</div>
									<div slot="desc" class="food-desc">
										<div class="left" v-if="option.bargain_type == 'huangou'">x{{option.num - option.discount_num}}</div>
										<div class="left" v-else>x{{option.num}}</div>
										<div class="right" v-if="option.total_price_show > option.total_discount_price_show">{{Lang.dollarSign}}{{option.total_price_show}}</div>
									</div>
								</van-card>
							</template>
						</template>
					</div>
					<van-cell-group class="extre-fee border-0px">
						<van-cell :title="cartItem.store.cn.box_price" class="border-0px">
							<template slot="right-icon">{{Lang.dollarSign}}{{allorder.orders[cartIndex].box_price}}</template>
						</van-cell>
						<van-cell :title="cartItem.store.cn.pack_fee" class="border-0px" v-if="allorder.orders[cartIndex].pack_fee > 0">
							<template slot="right-icon">{{Lang.dollarSign}}{{allorder.orders[cartIndex].pack_fee}}</template>
						</van-cell>
						<template v-if="allorder.orders[cartIndex].extra_fee > 0">
							<template v-if="allorder.orders[cartIndex].extra_fee_detail.length > 0" v-for="extraItem in allorder.orders[cartIndex].extra_fee_detail">
								<template v-if="extraItem.notshow != 1">
									<van-cell :title="extraItem.name" class="border-0px">
										<template slot="right-icon">{{Lang.dollarSign}}{{extraItem.fee}}</template>
									</van-cell>
								</template>
							</template>
						</template>
						<template v-if="allorder.orders[cartIndex].store_extra_fee_detail && allorder.orders[cartIndex].store_extra_fee_detail.length > 0">
							<van-cell :title="extraItem.name" class="border-0px" v-for="(extraItem, index) in allorder.orders[cartIndex].store_extra_fee_detail" :key="index">
								<div slot="right-icon">{{Lang.dollarSign}}{{extraItem.fee}}</div>
							</van-cell>
						</template>
					</van-cell-group>
					<div class="divide">
						<div class="divide-line"></div>
					</div>
					<van-cell-group class="discount-box border-0px">
						<template v-if="allorder.orders[cartIndex].activityed && allorder.orders[cartIndex].activityed.list">
							<template v-for="activity in allorder.orders[cartIndex].activityed.list" >
								<template v-if="activity.type != 'couponCollect' && activity.type != 'redPacket'">
									<van-cell class="border-0px">
										<div slot="title" class="discount-item flex">
											<img :src="'static/img/' + activity.type + '_b.png'" alt="">
											{{activity.name}}
										</div>
										<template slot="right-icon">
											<span class="c-danger">{{activity.text}}</span>
										</template>
									</van-cell>
								</template>
							</template>
						</template>
						<van-cell title="平台红包" class="border-0px">
							<template slot="right-icon" v-if=" allorder.orders[cartIndex].redpacket && allorder.orders[cartIndex].redpacket.id > 0">
								<span class="c-danger" @click="onToggleActive(cartIndex, cartItem.sid, 'redPacket')">-{{Lang.dollarSign}}{{allorder.orders[cartIndex].redpacket.discount}}</span>
							</template>
							<template slot="right-icon" v-else>
								<span class="c-danger" v-if="allorder.redPackets[cartIndex] && allorder.redPackets[cartIndex].length > 0" @click="onToggleActive(cartIndex, cartItem.sid, 'redPacket')">
									{{allorder.redPackets[cartIndex].length}}个可用红包
								</span>
								<template v-else>
									<span class="c-danger" @click="onToggleActive(cartIndex, cartItem.sid, 'redPacket')" v-if="svip_redpacket && svip_redpacket.id">您有可领取的超级会员红包</span>
									<span class="c-disabled" v-else>
										暂无可用红包
									</span>
								</template>
								<i class="van-icon van-icon-arrow van-cell__right-icon"></i>
							</template>
						</van-cell>
						<van-cell title="商家代金券" class="border-0px">
							<template slot="right-icon" v-if="allorder.orders[cartIndex].coupon && allorder.orders[cartIndex].coupon.id > 0">
								<span class="c-danger" @click="onToggleActive(cartIndex, cartItem.sid, 'coupon')">-{{Lang.dollarSign}}{{allorder.orders[cartIndex].coupon.discount}}</span>
							</template>
							<template slot="right-icon" v-else>
								<span class="c-danger" v-if="allorder.coupons[cartIndex] && allorder.coupons[cartIndex].length > 0" @click="onToggleActive(cartIndex, cartItem.sid, 'coupon')">
									{{allorder.coupons[cartIndex].length}}张可用代金券
								</span>
								<span class="c-disabled" v-else>
									暂无可用代金券
								</span>
								<i class="van-icon van-icon-arrow van-cell__right-icon"></i>
							</template>
						</van-cell>
						<van-cell class="flex-lr border-0px" title="备注" @click="util.jsUrl('/core/pages/order/note',{sid: cartItem.sid, type: 'note', channel: 'mall'}, 'replace')">
							<div slot="right-icon" class="note flex-lr">
								<div class="note-text" v-if="mallOrderExtra[cartItem.sid] && mallOrderExtra[cartItem.sid].note">
									{{mallOrderExtra[cartItem.sid].note}}
								</div>
								<span class="c-disabled" v-else>
									{{(cartItem.store.data && cartItem.store.data.order_note_placeholder) ? cartItem.store.data.order_note_placeholder : '口味、偏好等要求'}}
								</span>
								<i class="van-icon van-icon-arrow van-cell__right-icon"></i>
							</div>
						</van-cell>
						<van-cell class="flex-lr border-0px" title="发票" @click="util.jsUrl('/core/pages/order/note',{sid: cartItem.sid, type: 'invoice', channel: 'mall'}, 'replace')" v-if="cartItem.store.invoice_status == 1">
							<div slot="right-icon" class="note flex-lr">
								<div class="note-text" v-if="mallOrderExtra[cartItem.sid] && mallOrderExtra[cartItem.sid].invoiceId > 0">
									{{mallOrderExtra[cartItem.sid].invoiceTitle}}
								</div>
								<span class="c-disabled" v-else>不需要发票</span>
								<i class="van-icon van-icon-arrow van-cell__right-icon"></i>
							</div>
						</van-cell>

						<!--<div class="redpacket-buy" v-if="0 && order.meal_redpackets">
							<div class="flex-lr">
								<p class="buy-tip">{{order.meal_redpackets.title}} <router-link v-if="0"@click="onChangePopup('mealRedpacketRules')" tag="i" to="" class="icon icon-question1"></router-link></p>
								<span class="check-box flex" :class="{active: buy_mealredpacket == 1}" @click="onBuyMealredpacket">
									<i class="icon icon-check"></i>
								</span>
							</div>
							<div class="flex-lr">
								<p class="buy-total">{{order.meal_redpackets.placeholder}}</p>
								<p class="redpacket-price">
									<span class="old" v-if="0">{{Lang.dollarSign}}{{order.meal_redpackets.old_price}}</span>
									<span class="now">{{Lang.dollarSign}}{{order.meal_redpackets.price}}</span>
								</p>
							</div>
						</div>-->
					</van-cell-group>
					<div class="divide">
						<div class="divide-line"></div>
					</div>
					<van-cell-group class="border-0px">
						<van-cell>
							<div slot="title" class="order-pay-info">
								<div class='pay-price'>
									实付
									<div>{{Lang.dollarSign}}{{allorder.orders[cartIndex].final_fee_show}}</div>
								</div>
								<div class='discount-fee'>优惠{{Lang.dollarSign}}{{allorder.orders[cartIndex].discount_fee}}</div>
								<div class='total-original'>
									小计{{Lang.dollarSign}}{{allorder.orders[cartIndex].total_fee_show}}
								</div>
							</div>
						</van-cell>
					</van-cell-group>
				</div>
			</template>
			<van-cell-group class="margin-10-t" v-if="allorder.feetips && allorder.feetips.length > 0">
				<template v-for="(tip, tipIndex) in allorder.feetips">
					<template v-if="(tip.key == 'delivery_fee' && allorder.order_type == 1) || !tip.key">
						<van-cell :value="tip.value">
							<template slot="title">
								<span class="custom-title">{{tip.name}}</span>
								<span class="icon icon-question-circle" @click="onToggleDeliveryFee(tip.note)" v-if="tip.key == 'delivery_fee'"></span>
							</template>
						</van-cell>
					</template>
				</template>
			</van-cell-group>
		</div>

		<!-- 红包弹出层 -->
		<van-popup v-model="popup.redPacket" position="bottom" v-if="(allorder.redPackets && allorder.redPackets[activeIndex] && allorder.redPackets[activeIndex].length > 0) || (svip_redpacket && svip_redpacket.id)">
			<div class="popup-redpacket">
				<div class="popup-title van-hairline--bottom">平台红包</div>
				<div class="popup-container">
					<template v-if="svip_redpacket && svip_redpacket.id">
						<load type="loaded" text="超级会员专享红包" bgcolor="#f5f5f5"></load>
						<div class="svip-redpacket" v-if="is_buysvip == 1">
							<div class="svip-redpacket-item">
								<div class="svip-redpacket-top flex">
									<div class="svip-redpacket-left flex">
										<div class="redpacket-img">
											<img class="img-100" :src="svip_redpacket.logo" alt="" v-if="svip_redpacket.sid > 0">
											<img class="img-100" src="static/img/svip_redpacket.png" alt="" v-else>
										</div>
										<div class="redpacket-info">
											<div class="redpacket-title">{{svip_redpacket.title}}</div>
											<div>
												<span class="redpacket-tips">超级会员</span>
												<span class="font-12 c-gray"> 限会员有效期内使用</span>
											</div>
										</div>
									</div>
									<div class="svip-redpacket-right">
										<div class="c-danger">
											<div class="redpacket-money">
												<span class="redpacket-unit">{{Lang.dollarSign}}</span>
												{{svip_redpacket.discount}}
											</div>
										</div>
										<div class="font-12 c-gray" v-if="svip_redpacket.condition == 0">无门槛</div>
										<div class="font-12 c-gray" v-else-if="svip_redpacket.condition > 0">满{{svip_redpacket.condition}}{{Lang.dollarSignCn}}</div>
									</div>
								</div>
								<div class="svip-redpacket-line">
									<div class="line"></div>
								</div>
								<div class="svip-redpacket-bottom flex-lr">
									<div class="font-12 c-gray">{{svip_redpacket.desc}}</div>
									<div class="svip-redpacket-convert" @click="onSelectRedpacket('svip_' + svip_redpacket.store_redpacket.id)" v-if="svip_redpacket.store_redpacket && svip_redpacket.store_redpacket.id">立即使用</div>
									<div class="svip-redpacket-convert" @click="onSelectRedpacket(svip_redpacket.id)" v-else>立即使用</div>
								</div>
							</div>
						</div>
						<div class="svip-redpacket" v-else>
							<div class="svip-redpacket-item">
								<div class="svip-redpacket-top flex">
									<div class="svip-redpacket-left flex">
										<div class="redpacket-img">
											<img class="img-100" :src="svip_redpacket.logo" alt="" v-if="svip_redpacket.sid > 0">
											<img class="img-100" src="static/img/svip_redpacket.png" alt="" v-else>
										</div>
										<div class="redpacket-info">
											<div class="redpacket-title">{{svip_redpacket.title}}</div>
											<div>
												<span class="redpacket-tips">超级会员</span>
												<span class="font-12 c-gray"> 限{{svip_redpacket.endtime_cn}}前领取</span>
											</div>
										</div>
									</div>
									<div class="svip-redpacket-right">
										<div class="c-danger">
											<div class="redpacket-money">
												<span class="redpacket-unit">{{Lang.dollarSign}}</span>
												{{svip_redpacket.discount}}
											</div>
										</div>
										<div class="font-12 c-gray" v-if="svip_redpacket.condition == 0">无门槛</div>
										<div class="font-12 c-gray" v-else-if="svip_redpacket.condition > 0">满{{svip_redpacket.condition}}{{Lang.dollarSignCn}}</div>
									</div>
								</div>
								<div class="svip-redpacket-line">
									<div class="line"></div>
								</div>
								<div class="svip-redpacket-bottom flex-lr">
									<div class="font-12 c-gray">{{svip_redpacket.desc}}</div>
									<template v-if="svip_redpacket.take_status == 'take'">
										<div class="svip-redpacket-convert" @click="onConfirmSvipExchange">立即领取</div>
									</template>
									<template v-else-if="svip_redpacket.take_status == 'exchange'">
										<div class="svip-redpacket-convert" @click="onChangePopup('svipRedpacket')">立即兑换</div>
									</template>
								</div>
							</div>
						</div>
					</template>
					<load type="loaded" :text="'可用红包(' + allorder.redPackets[activeIndex].length + '个)'" bgcolor="#f5f5f5"></load>
					<div class="redPacket-list content-padded" v-for="(item, index) in allorder.redPackets[activeIndex]" :key="index">
						<div class="redPacket-list-item" @click="onSelectRedpacket(item.id)">
							<div class="redPacket-list-item-container">
								<div class="redPacket-info row">
									<div class="col-50">
										<span class="redPacket-title">{{item.title}}</span>
									</div>
									<div class="col-50 text-right">
										<div class="price">{{Lang.dollarSign}}<span class="price-num">{{item.discount}}</span></div>
									</div>
								</div>
								<div class="redPacket-use-limit row">
									<div class="col-60">{{item.day_cn}}</div>
									<div class="col-40 text-right">
										<p class="use-condition">满{{item.condition}}{{Lang.dollarSignCn}}可用</p>
									</div>
								</div>
							</div>
							<span class="circle circle-left"></span>
							<span class="circle circle-right"></span>
							<div class="selected-status" v-if="allorder.orders[activeIndex].redpacket && item.id == allorder.orders[activeIndex].redpacket.id">
								<img src="static/img/success.png" alt="">
							</div>
						</div>
					</div>
				</div>
				<div class="popup-cancle van-hairline--top" @click="onSelectRedpacket(0)">不使用红包</div>
			</div>
		</van-popup>

		<!-- 代金券弹出层 -->
		<van-popup v-model="popup.coupon" position="bottom" v-if="allorder.coupons[activeIndex] && allorder.coupons[activeIndex].length > 0">
			<div class="popup-coupon">
				<div class="popup-title van-hairline--bottom">商家代金券</div>
				<div class="popup-container">
					<load type="loaded" :text="'可用代金券(' + allorder.coupons[activeIndex].length + '张)'" bgcolor="#f5f5f5"></load>
					<div class="coupon-list">
						<div class="content-padded">
							<div class="coupon-item" v-for="item in allorder.coupons[activeIndex]">
								<div class="clearfix" @click="onSelectCoupon(item.id, item.sid)">
									<span class="circle circle-left"></span>
									<span class="circle circle-right"></span>
									<div class="left">
										<div class="store-logo">
											<img :src="item.logo" alt="">
										</div>
										<div class="coupon-detail">
											<div class="coupon-title">{{item.title}}</div>
											<div class="use-time">有效期至:{{item.endtime_cn}}</div>
										</div>
									</div>
									<div class="right">
										<div class="price">
											<span>{{Lang.dollarSign}}</span>{{item.discount}}
										</div>
										<div class="condition">满{{item.condition}}{{Lang.dollarSignCn}}可用</div>
									</div>
								</div>
								<div class="selected-status" v-if="allorder.orders[activeIndex].coupon && item.id == allorder.orders[activeIndex].coupon.id">
									<img src="static/img/success.png" alt="">
								</div>
							</div>
						</div>
					</div>
				</div>
				<div class="popup-cancle van-hairline--top" @click="onSelectCoupon(0, activeSid)">不使用代金券</div>
			</div>
		</van-popup>

		<!--配送时间弹出层-->
		<van-popup v-model="popup.deliveryTime" position="bottom" v-if='allorder.deliveryTimes.predict_day && allorder.deliveryTimes && allorder.deliveryTimes.times'>
			<div class="popup-time">
				<div class="popup-title van-hairline--bottom">选择预计送达时间</div>
				<div class="popup-container">
					<div class="left">
						<template v-for="(item, index) in allorder.deliveryTimes.days">
							<div class="date-item van-hairline--bottom" :class="{'van-hairline--right': allorder.deliveryTimes.predict_day != item, 'active': allorder.deliveryTimes.predict_day == item}" @click="onSelectDeliveryday(item)">{{item}}</div>
						</template>
					</div>
					<div class="right">
						<van-cell-group v-if="allorder.deliveryTimes.times[allorder.deliveryTimes.predict_day] && allorder.deliveryTimes.times[allorder.deliveryTimes.predict_day].times">
							<template v-for="(item, index) in allorder.deliveryTimes.times[allorder.deliveryTimes.predict_day].times">
								<van-cell @click="onSelectDeliverytimes(item.time_cn, index)" :class="{active: allorder.deliveryTimes.predict_index==index}">
									<template slot="title">
										{{item.time_cn}}{{item.time_cn == '立即送出' ? '(' + item.start + '~' + item.end + ')' : ''}}
									</template>
									<template slot="right-icon">
										{{allorder.order_type == 1 ? item.total_delivery_price_cn : ''}}
										<van-icon name="check" class="van-cell__right-icon" :class="{active: allorder.deliveryTimes.predict_index==index}"/>
									</template>
								</van-cell>
							</template>
						</van-cell-group>
					</div>
				</div>
				<div class="popup-cancle van-hairline--top" @click="onChangePopup('deliveryTime')">取消</div>
			</div>
		</van-popup>
		<van-dialog v-model="popup.deliveryFeeShow" title="配送费说明" class="popup-deliveryFee">
			<template v-if="allorder.orders[activeIndex] && allorder.orders[activeIndex].delivery_fee_note">
				<div class="fee-item" v-for="item in allorder.orders[activeIndex].delivery_fee_note.notes">
					{{item}}
				</div>
			</template>
		</van-dialog>
		<van-dialog v-model="popup.allDeliveryFeeShow" title="配送费说明" class="popup-deliveryFee">
			<template v-if="allDeliveryFeeNote">
				<div class="fee-item" v-for="item in allDeliveryFeeNote.notes">
					{{item}}
				</div>
			</template>
		</van-dialog>
		<transition name="loading">
			<iloading v-if="showPreLoading"></iloading>
		</transition>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import Load from '@/components/load'
	import { Dialog } from 'vant';
	import {mapState, mapMutations} from 'vuex'
	export default {
		data() {
			return {
				carts: [],
				allorder: {
					orders: [],
					address: {},
					deliveryTimes: {
						predict_index: '',
						predict_day: '',
						predict_day_cn: '',
						predict_time_cn: '',
						predict_year_cn: '',
					},
					feetips: [],
					mobile: '',
					coupons: [],
					errormsg: []
				},
				allDeliveryFeeNote: {},
				popup:{
					coupon: false,
					redPacket: false,
					deliveryTime: false,
					picknum: false,
					mealRedpacketRules: false,
					svipRedpacket: false,
					zhunshibaoAgreementShow: false,
					yinsihaoAgreementShow: false,
					deliveryFeeShow: false,
					allDeliveryFeeShow: false
				},
				activeSid: 0,
				activeIndex: 0,
				query: {
					channel: 'mall'
				},
				config_takeout: {},

				svip_redpacket: {},
				redPackets: [],
				address: {},
				store: {
					cn: {}
				},
				order: {},
				coupons: [],
				mobile: '',
				islegal: false,
				showPreLoading: true,
			}
		},
		computed: {

		},
		methods: {
			onLoad() {
				var that = this;
				var params = {
					extra: JSON.stringify(this.mallOrderExtra),
				};
				that.util.request({
					url: 'wmall/mall/order/index',
					data: params,
				}).then((res) => {
					that.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						that.util.$toast(result.message, that.util.getUrl({path: '/mall/pages/cart/index'}), 1500, 'replace');
						return;
					}
					result = result.message;
					var tempExtra = Object.assign(this.mallOrderExtra, {
						predict_index: result.allorder.deliveryTimes.predict_index,
						predict_time_cn: result.allorder.deliveryTimes.predict_time_cn,
						predict_day_cn: result.allorder.deliveryTimes.predict_day_cn,
						address_id: result.allorder.address.id,
						order_type: result.allorder.order_type,
						mobile: result.allorder.mobile
					});
					this.replaceMallOrderExtra(tempExtra);
					that.carts = [...result.carts];
					that.allorder = that.util.extend(that.allorder, result.allorder);
					that.config_takeout = that.util.extend(that.config_takeout, result.config_takeout);
					that.islegal = result.islegal;
					var errormsg = result.allorder.errormsg;
					if(errormsg && errormsg.length > 0) {
						for(var i in errormsg) {
							that.$dialog.alert({
								message: errormsg[i].message
							})
						}
					}
				})
			},

			onSubmit() {
				var that = this;
				if(that.mallOrderExtra.order_type == 2) {
					if(!that.mallOrderExtra.mobile) {
						that.util.$toast('请输入提货手机号');
						return false;
					}
					if(!that.util.isValidMobile(that.mallOrderExtra.mobile)) {
						that.util.$toast('请输入正确的手机号');
						return false;
					}
					if(!that.islegal) {
						return false;
					}
					that.islegal = false;
					that.submitPost();
				} else {
					if(!that.mallOrderExtra.address_id) {
						that.util.$toast('请选择收货地址');
						return false;
					}
					if(that.config_takeout.audit_accept_address == 1) {
						that.$dialog.confirm({
							title: '请确定您的收货信息',
							message: '收货地址：' + that.allorder.address.address + '-' + that.allorder.address.number +'</br>' + '手机　号：' + that.allorder.address.mobile + '</br>' + '收货　人：' + that.allorder.address.realname,
						}).then(() => {
							that.submitPost();
							return;
						}).catch(() => {
							that.islegal = true;
						});
					} else {
						that.submitPost();
					}
				}
			},

			submitPost: function() {
				var that = this;
				var params = {
					extra: JSON.stringify(that.mallOrderExtra),
				};
				that.util.request({
					url: 'wmall/mall/order/submit',
					data: params,
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						that.util.$toast(result.message);
						if(result.errno == 1000) {
							that.$router.replace(that.util.getUrl({path: '/mall/pages/cart/index'}));
							return false;
						}
						return false;
					}
					var parent_oid = result.message;
					this.replaceMallOrderExtra({});
					this.$router.replace(this.util.getUrl({path: '/pages/public/pay', query: {order_id: parent_oid, order_type: 'mall'}}));
				})
			},

			onCalculate() {
				var that = this;
				var params = {
					extra: JSON.stringify(this.mallOrderExtra),
				};
				this.util.request({
					url: 'wmall/mall/order/index',
					data: params,
					method: 'post'
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						that.util.$toast(result.message);
						return;
					}
					result = result.message;
					that.carts = [...result.carts];
					that.allorder = that.util.extend(that.allorder, result.allorder);
					that.islegal = result.islegal;
				})
			},

			onToggleDeliveryFee(note) {
				console.log(note);
				this.allDeliveryFeeNote = note;
				this.onChangePopup('allDeliveryFeeShow');
			},

			onChangeOrderType(type) {
				this.setMallOrderExtra({sid: 0, key: 'order_type', val: type});
				this.onCalculate();
			},


			onSelectDeliveryday(day) {
				this.$set(this.allorder.deliveryTimes, 'predict_day', day);
				this.$set(this.allorder.deliveryTimes, 'predict_day_cn', day);
				this.setMallOrderExtra({sid: 0, key: 'predict_day_cn', val: day});
			},

			onSelectDeliverytimes: function(time, index) {
				this.$set(this.allorder.deliveryTimes, 'predict_index', index);
				this.$set(this.allorder.deliveryTimes, 'predict_time_cn', time);
				this.onChangePopup('deliveryTime');
				this.setMallOrderExtra({sid: 0, key: 'predict_index', val: index});
				this.setMallOrderExtra({sid: 0, key: 'predict_time_cn', val: time});
				this.onCalculate();
			},

			onSelectRedpacket(id) {
				this.setMallOrderExtra({sid: this.activeSid, key: 'redpacket_id', val: id});
				this.onChangePopup('redPacket');
				this.onCalculate();
			},

			onSelectCoupon(id, sid) {
				this.setMallOrderExtra({sid: sid, key: 'coupon_id', val: id});
				this.onChangePopup('coupon');
				this.onCalculate();
			},

			onToggleActive(index, sid, popupType) {
				var that = this;
				that.activeIndex = index;
				that.activeSid = sid;
				that.onChangePopup(popupType)
			},

			onChangePopup(key) {
				this.popup[key] = !this.popup[key];
			},

			onBlur() {
				var that = this;
				//处理input框失焦时，页面下部留白的问题
				window.scroll(0, 0);
			},
			...mapMutations([
				'setMallOrderExtra', 'replaceMallOrderExtra'
			]),
		},

		created() {
			var query = this.util.parseQuery(this.$route.query);
			this.query = this.util.extend(this.query, query);
		},

		mounted() {
			var that = this;
			that.onLoad();
			that.util.icloudapi();
		},

		watch: {
			'allorder.mobile': {
				handler: function(value) {
					this.setMallOrderExtra({sid: 0, key: 'mobile', val: value});
				},
			}
		},

		computed: {
			...mapState([
				'mallOrderExtra'
			]),
		},

		components: {
			PublicHeader,
			Load
		},
	}
</script>

