		color: #ff2d4b;
		margin-top: 3px;
	}
	#mall-order-create .coupon-item .left .coupon-detail .use-time{
		font-size: 12px;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		color: #808080;
		margin-top: 16px;
	}
	#mall-order-create .coupon-item .right{
		width: 29%;
		border-left: 1px dashed #ccc;
		text-align: center;
		float: right;
	}
	#mall-order-create .coupon-item .right .price{
		color: #ff2d4b;
		font-size: 24px;
	}
	#mall-order-create .coupon-item .right .price span{
		font-size: 14px;
		margin-right: 2px;
	}
	#mall-order-create .coupon-item .right .condition{
		margin-top: 8px;
		font-size: 12px;
		display: -webkit-box;
		overflow: hidden;
		text-overflow: ellipsis;
		word-wrap: break-word;
		white-space: normal !important;
		-webkit-line-clamp: 2;
		-webkit-box-orient: vertical;
		padding: 2px;
		color: #808080;
	}
	#mall-order-create .coupon-item .selected-status,#mall-order-create .redPacket-list-item .selected-status{
		position: absolute;
		right: 0;
		bottom: 0;
		width: 31px;
		height: 24px;
	}
	#mall-order-create .coupon-item .selected-status img,#mall-order-create .redPacket-list-item .selected-status img{
		width: 100%;
		height: 100%;
	}
	#mall-order-create .member-address{
		font-size: 14px;
		font-weight: bold;
	}
	#mall-order-create .member-address .tag-address{
		display: inline-block;
		font-size: 12px;
		width: 35px;
		text-align: center;
		line-height: 1;
		padding: 3px 0;
		margin-right: 5px;
		flex: 1;
	}
	#mall-order-create .member-address .tag-address.tag-address-yellow{
		background-color: #FEF2D7;
		color: #F3AE18;
	}
	#mall-order-create .member-address .tag-address.tag-address-blue{
		background-color: #E2EDFC;
		color: #5996E9;
	}
	#mall-order-create .member-address .tag-address.tag-address-green{
		background-color: #EDF9E6;
		color: #8FCE5F;
	}
	#mall-order-create .popup-time, #mall-order-create .popup-coupon, #mall-order-create .popup-redpacket{
		width: 100%;
		height: 400px;
		overflow: hidden;
	}
	#mall-order-create .popup-time .popup-container{
		width: 100%;
		height:350px;
		display: flex;
		flex-flow: row nowrap;
		justify-content: space-between;
	}
	#mall-order-create .popup-time .popup-container .left{
		width: 35%;
		height: 100%;
		overflow-y: auto;
		background-color: #f5f5f5;
	}
	#mall-order-create .popup-time .popup-container .right{
		width: 65%;
		height: 100%;
		overflow-y: auto;
	}
	#mall-order-create .popup-time .popup-container .left::-webkit-scrollbar,#mall-order-create .popup-time .popup-container .right::-webkit-scrollbar{
		width: 0px;
		height: 0px;
	}
	#mall-order-create .popup-time .left .date-item{
		width: 100%;
		height: 44px;
		text-align: center;
		line-height: 44px;
		box-sizing: border-box;
		background-color: #fff;
	}
	#mall-order-create .popup-time .left .date-item.active{
		color: #ff2d4b;
	}
	#mall-order-create .popup-time .right .van-cell.active{
		color: #ff2d4b;
	}
	#mall-order-create .popup-time .right .van-icon{
		color: #fff;
		font-size: 16px;
	}
	#mall-order-create .popup-time .right .van-icon.active{
		color: #ff2d4b;
	}
	#mall-order-create .popup-time .popup-cancle, #mall-order-create .popup-coupon .popup-cancle, #mall-order-create .popup-redpacket .popup-cancle{
		width: 100%;
		height:50px;
		text-align:center;
		line-height:50px;
		font-size:16px;
		color:#FF2D4B;
	}

	#mall-order-create .redPacket-list.content-padded{
		margin: 0 10px 10px 10px;
	}
	#mall-order-create .redPacket-list .row{
		margin-left: -4%;
		overflow: hidden;
	}
	#mall-order-create .redPacket-list .redPacket-list-item{
		background: #fff;
		margin-bottom: 12px;
		overflow: hidden;
		position: relative;
	}
	#mall-order-create .redPacket-list .redPacket-list-item .redPacket-list-item-container{
		padding: 25px 16px;
	}
	#mall-order-create .redPacket-list .redPacket-list-item .redPacket-list-item-container.superredpacket-meal{
		border: 2px solid #ff2d4b;
	}
	#mall-order-create .redPacket-list .redPacket-info .col-50{
		width: 46%;
		margin-left: 4%;
		float: left;
	}
	#mall-order-create .redPacket-list .redPacket-info .col-50 .redPacket-title{
		color: #ff2d4b;
		margin: 0;
		font-size: 18px;
	}
	#mall-order-create .redPacket-list .redPacket-info .col-50 .price{
		color: #ff2d4b;
		position: relative;
		margin-top: -4px;
	}
	#mall-order-create .redPacket-list .redPacket-info .col-50 .price .price-num{
		font-size: 22px;
		line-height: 26px;
	}
	#mall-order-create .redPacket-list .redPacket-list-item .redPacket-list-item-container .redPacket-use-limit{
		margin-top: 10px;
		color: #666;
		font-size: 14px;
	}
	#mall-order-create .redPacket-list .row .col-60{
		width: 55.99999999999999%;
		margin-left: 4%;
		float: left;
		white-space:nowrap
	}
	#mall-order-create .redPacket-list .row .col-40{
		width: 36%;
		margin-left: 4%;
		float: left;
	}
	#mall-order-create .redPacket-list .row .col-40 .use-condition{
		margin: 0;
		font-size: 14px;
	}
	#mall-order-create .redPacket-list .redPacket-list-item .redPacket-list-item-container .other-limit{
		font-size: 12px;
		color: #999;
		border-top: 1px dashed #e0e0e0;
		padding-top: 4px;
	}
	#mall-order-create .redPacket-list .redPacket-list-item .redPacket-list-item-container .other-limit{
		font-size: 12px;
		color: #999;
		border-top: 1px dashed #e0e0e0;
		padding-top: 4px;
	}
	#mall-order-create .redPacket-list .redPacket-list-item .circle{
		display: block;
		width: 16px;
		height: 16px;
		background: #f5f5f5;
		position: absolute;
		border-radius: 16px;
		top:50%;
		margin-top: -8px;
		z-index: 20;
	}
	#mall-order-create .redPacket-list .redPacket-list-item .circle.superredpacket-meal{
		border: 2px solid #ff2d4b;
	}
	#mall-order-create .redPacket-list .redPacket-list-item .circle-left{
		left: -8px;
	}
	#mall-order-create .redPacket-list .redPacket-list-item .circle-right{
		right: -8px;
	}
	#mall-order-create .redPacket-list .redPacket-list-item .overdue{
		display: block;
		position: absolute;
		width: 70px;
		height: 70px;
		top: 10px;
		right: 10px;
		background: url(../../../../static/img/ws.png);
		background-size: 100%;
		z-index: 100;
	}
	#mall-order-create .redPacket-list .redPacket-list-item .use{
		display: block;
		position: absolute;
		width: 70px;
		height: 70px;
		top: 10px;
		right: 10px;
		background: url(../../../../static/img/wu.png);
		background-size: 100%;
		z-index: 100;
	}
	#mall-order-create .popup-deliveryFee{
		padding: 0 15px;
	}
	#mall-order-create .popup-deliveryFee .fee-item{
		color: #ccc;
		padding: 5px 0;
	}
</style>


